.banner {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  padding-left: 37px;
  border-radius: 20px;
  color: #ffff;
  font-size: 18px;
  background-position: top right;
  background-size: contain;
  background-repeat: no-repeat;
}

.title {
  font-size: 2.61em;
  font-weight: 900;
  color: currentColor;
  line-height: 50px;
  margin-bottom: 24px;
}

.subtitle {
  font-size: 28px;
  color: currentColor;
  line-height: 32px;
}

.price {
  font-size: 28px;
  color: currentColor;
  line-height: 32px;
}

.banner_middle {
  font-size: 11px;
  max-width: 490px;
  min-height: 220px;
}

.banner_big {
  width: 100%;
  font-size: 16px;
  min-height: 260px;
}
