.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
}

.title {
  color: #000000;
  margin: 24px 0px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 28px;
  display: flex;
  align-items: center;
}

.infoText {
  color: #7b8e98;
  margin: 20px 0px;
  font-family: Arial, Helvetica, sans-serif;
  font-style: normal;
  font-size: 12px;
  line-height: 14px;
}

.link {
  cursor: pointer;
  text-align: right;
}

.errorMessage {
  font-family: Arial, Helvetica, sans-serif;
  text-align: end;
  color: red;
  font-size: 12px;
  align-self: flex-end;
}
